import React from 'react';
import { SearchBar,WingBlank } from 'antd-mobile';
import {withRouter} from 'react-router-dom';


class SearchBarExample2 extends React.Component {
  state = {
    value: '美食',
  };

  onChange= (value) => {
    this.setState({ value });
  };
  clear = () => {
    this.setState({ value: '' });
  };
  handleClick = () => {
    this.manualFocusInst.focus();
  }
  render() {
    return (<div>
     
      <WingBlank><div className="sub-title">第二个页面</div></WingBlank>
      <SearchBar
       value={this.state.value}
       
        onSubmit={value => console.log(value, 'onSubmit')}
        onClear={value => console.log(value, 'onClear')}
        onBlur={(props) => 
            console.log('onBlur')
        }
        onCancel={() => 
            // console.log('onl'),
            this.props.history.go(-1)
        }
        showCancelButton
        onChange={this.onChange}
      />
    </div>);
  }
}

export default withRouter(SearchBarExample2);