import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import Header from './pages/Header';
import routes from './router.config';
import {Button,TabBar} from 'antd-mobile'
import Mytab from './taobao/Mytab';
import Home from './taobao/Home';
import Cart from './taobao/Cart';
import Login from './taobao/Login';
import List1 from './taobao/List1';
import Store from './taobao/Store';

import SearchBarExample2 from './taobao/Page';
import FlexExample from './taobao/Things';
import ThingDetail from './taobao/Details';
import Search from './taobao/Search';
import {Provider} from 'react-redux';
// 1、搜索框（点击跳转搜索页面，顶部是searchBar，
// 点取消，返回首页）
// 2、轮播图
// 3、商品列表（10个）（点击跳转商品详情页，
// 详情页写上面的图片和返回箭头，点返回箭头返回首页
// 和底部按钮（店铺、客服、收藏、加入购物车和立即购买）
// ）
const App = ()=>{
	return(
		<Provider store={Store}> 
		<Router>
			<Switch>
				<Route exact path='/' component={Home} />
				<Route path='/cart' component={Cart} />
				<Route path='/Mytab' component={Mytab} />
				<Route path='/Page' component={SearchBarExample2} />
				<Route path='/things' component={FlexExample} />
				<Route path='/details' component={ThingDetail} />
				<Route path='/login' component={Login} />
				<Route path='/list1' component={List1} />
				<Route component={Home} />


			</Switch>
			{/* <Mytab /> */}
		</Router>
	</Provider>
	)
}
export default App;



