import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Ourtab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    {/* 店铺 */}
                    <TabBar.Item
                        title="店铺"
                        key="dianpu"
                        icon={
                            <i className='iconfont icon-taobaodianpu'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-taobaodianpu'></i>                           
                        }
                        selected={ pathname === '/' }
                        onPress={ () => {
                            this.props.history.push('/');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>


                    {/* 客服 */}
                    <TabBar.Item
                        title="客服"
                        key="kefu"
                        icon={
                            <i className='iconfont icon-taobaokefu'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-taobaokefu'></i>                           
                        }
                        selected={ pathname === '/' }
                        onPress={ () => {
                            this.props.history.push('/');
                        } }
                        data-seed="logId"
                    ></TabBar.Item>

                    {/* 收藏 */}
                    <TabBar.Item
                        title="收藏"
                        key="shoucang"
                        icon={
                            <i className='iconfont icon-tubiaozhizuomoban-01'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-tubiaozhizuomoban-01'></i>                           
                        }
                        selected={ this.state.selectedTab=='yellowTab'}
                        onPress={ () => {
                            this.setState({
                                selectedTab:'yellowTab'
                            })
                            // this.props.history.push('/')
                            
                        } }
                        data-seed="logId"
                    ></TabBar.Item>


                    {/* 加入购物车 */}
                    <TabBar.Item
                        // title="加入购物车"
                        key="jiaru"
                        icon = {
                            <div style={{width:'120px',height:'30px',backgroundColor:'orange'}}>
                                <p style={{display:'inline-block',color:'white',marginTop:'8px'}}>加入购物车</p>
                            </div>
                        }
                    ></TabBar.Item>

                    {/* 立即购买  */}
                    <TabBar.Item
                        key="jiaru"
                        icon = {
                            <div style={{width:'120px',height:'30px',backgroundColor:'red',marginRight:'8px'}}>
                                <p style={{display:'inline-block',color:'white',marginTop:'8px'}}>立即购买</p>
                            </div>
                        }
                    ></TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Ourtab);