import React from 'react';
import { Flex } from 'antd-mobile';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';

class FlexExample1 extends React.Component {
    constructor(props) {
        super(props);
    }
    state = {
        imgObj: [],
    }
    componentDidMount = () => {
        fetch('https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist')
            .then((res) => res.json())
            .then((myJson) => {
                console.log(myJson);
                let ant = myJson.data;
                this.setState({
                    imgObj:ant
                })
                this.props.dispatch({
                    type:'aaa',
                    value:ant
                })
                this.props.dispatch({
                    type:'bbb',
                    value:ant
                })
            });
    } 
    getImage(a) {
        let arr = this.state.imgObj;
        for (var i = a; i < arr.length; i++) {
            return (
                 <Flex>
                    <Flex.Item>
                        <img style={{ width:'190px',height:'190px'}}
                            onClick={() => {
                                this.props.dispatch({
                                    type:'item11',
                                    index:i
                                })
                                this.props.history.push("./List1")
                            }}
                            src={arr[i].img} >
                        </img>
                        <p>{arr[i].price}</p>
                        <p>{arr[i].title}</p>
                    </Flex.Item>
                    <Flex.Item>
                        <img style={{ width:'190px',height:'190px'}}
                            onClick={() => {
                                this.props.dispatch({
                                    type:'aaa',
                                    index:i+1
                                })
                                this.props.history.push("/List1");
                            }}
                            src={arr[i + 1].img}>
                        </img>
                        <p>{arr[i + 1].price}</p>
                        <p>{arr[i + 1].title}</p>
                    </Flex.Item>
                 </Flex>
            )
        }
    }
    render() {
        console.log("zhangcahohui:", this.props.userinfo);
        return (
            <div>
                <div className="ggg">
                    <div className="ooo">
                        {this.getImage(0)}
                        {this.getImage(2)}
                        {this.getImage(4)}
                        {this.getImage(6)}
                        {this.getImage(8)} 
                    </div>
                </div> 
            </div>
        )
    }
}
const zhang = (state)=>{
    return{
        mydata:state.todo,
        change:state.change,
        userinfo:state.login
    }
}
export default connect(zhang)(withRouter(FlexExample1));