import React from 'react';
import Ourtab from './Ourtab';
import { Flex } from 'antd-mobile';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';


const List1= (props) => {
    console.log(props);
    let index=props.change;
    const imgobj=props.mydata[index];
    const str = imgobj.img+'';
    console.log(str);
    return (
        <div>
            <span onClick={() => props.history.goBack("/Home")}>返回</span>
            <div className="ggg">
                <div className="ooo"></div>
                <Flex>
                    <Flex.Item>
                        <img src={str}></img>
                    </Flex.Item>
                </Flex>
            </div>
            <Ourtab />
        </div>
    )
}

const c = (state)=>{
    return {
        mydata:state.todo,
        change:state.change
    }
}
export default connect(c)(withRouter(List1));