import React from 'react';
import { SearchBar, Button, WhiteSpace, WingBlank } from 'antd-mobile';
import {withRouter} from 'react-router-dom';


class SearchBarExample extends React.Component {
  state = {
    value: '寻找店铺宝贝',
  };

  onChange= (value) => {
    this.setState({ value });
  };
  clear = () => {
    this.setState({ value: '' });
  };
  handleClick = () => {
    this.manualFocusInst.focus();
  }
  render() {
    return (
    <div style={{width:'100%',height:'40px',backgroundColor:'#FFAA33'}}>
      <i style={{fontSize:40,color:'red',float:'left'}} className='iconfont icon-taobao-'></i>
      <Button 
        onClick={(props)=>{this.props.history.push('./Page')}}
        style={{width:'330px',height:'30px',top:'7px',left:'3px',backgroundColor:'	#FF5511'}}>
        <span style={{position:'relative',top:'-4px'}}>寻找宝贝店铺</span>
      </Button>
    
      
    </div>);
  }
}

export default withRouter(SearchBarExample);